import { createBrowserRouter } from "react-router-dom";
import LayOut from "../components/LayOut/LayOut";
import { Events, Profile, Places, ErrorPage } from "../views";

export const router = createBrowserRouter([
  {
    path: "/",
    element: <LayOut />,
    errorElement: <ErrorPage />,
    children: [
      {
        path: "",
        element: <Places />,
        index: true,
      },
      {
        path: "events",
        element: <Events />,
      },
      {
        path: "profile",
        element: <Profile />,
      },
    ],
  },
]);
